<template>
  <div class="container">
    <div class="carousel-container">
      <h1>{{id ? '编辑' : '新建'}}首页轮播图</h1>
      <el-form label-width="120px" @submit.native.prevent="save">
        <el-form-item label="跳转链接">
          <el-input v-model="ads.path"></el-input>
        </el-form-item>
        <el-form-item label="图片">
          <el-upload
            class="avatar-uploader carousel"
            :action="uploadUrl"
            :headers="setHeader"
            :on-success="uploadSuccess"
            :show-file-list="false"
          >
            <img v-if="ads.imgUrl" :src="ads.imgUrl" class="width-100p height-100p" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "carouselCreate",
  props: {
    id: {}
  },
  data() {
    return {
      ads: {
        //跳转链接
        path: "",
        //图片地址
        imgUrl: ""
      }
    };
  },
  created() {
    this.id && this.getCarousel();
  },
  methods: {
    //获取首页轮播图信息
    getCarousel() {
      let url = `rest/ads/${this.id}`;
      this.$http.get(url).then(res => {
        let { data } = res;
        this.ads.imgUrl = data.imgUrl;
        this.ads.path = data.path;
      });
    },
    //保存
    save() {
      let { id, ads } = this;
      let url = "rest/ads";
      let method;
      let data = ads;

      if (id) {
        url = `${url}/${id}`;
        method = "put";
      } else {
        method = "post";
      }

      this.$http({
        method,
        url,
        data
      }).then(() => {
        this.$router.push("/ads/list");
      });
    },
    //图片上传完成
    uploadSuccess(res) {
      this.ads.imgUrl = res.url;
    }
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  width: 350px;
  height: 200px;
  border-radius: 0;
}
.avatar-uploader .el-upload:hover {
  width: 350px;
  height: 200px;
}
.avatar-uploader-icon {
  line-height: 200px;
}

.icon {
  width: 350px;
  height: 200px;
  width: 100%;
}
img {
  height: 50px;
}
</style>